	/**
	 * 
	 *
	 * @param rule rulesminmaxmessage
	 * @param value 
	 * @param callback 
	 */
	validateNumberRange: function(rule, value, callback) {
		let min = rule.min == null || rule.min === '' || isNaN(rule.min) ? -Infinity : rule.min,
            max = rule.max == null || rule.max === '' || isNaN(rule.max) ? Infinity : rule.max;
		// nullundefined
		if(value === '' || value == null || isNaN(value)) {
			callback();
		}
		if(value >= min && value <= max) {
			callback();
		} else {
			callback(Error(rule.message));
		}
	},
